import React from 'react';
import styled from 'styled-components';
import Card from 'react-bootstrap/Card';
import Image from 'react-bootstrap/Image'
import Me from '../../me.jpg';
import Resume from '../../Bella_Le_Resume.pdf';
import Emoji from './Emoji';

const StyledCard = styled(Card)`
    width: auto;
`;

const StyledDiv = styled.div`
    margin: auto;
    margin-bottom: 20px;
    text-align: center;
    width: 80%;
    @media (min-width: 500px) {
        width: auto;
    }

    @media (min-width: 800px) {
        margin-left: 10px;
        float: right;
        width: 250px;
    }
`;

const StyledLink = styled.a`
    color: #890067;
    &:focus, &:hover, &:visited, &:link, &:active {
        text-decoration: none;
    }
    &:hover {
        color: #E7A8D7;
    }
`;

const Landing = () => (
    <StyledCard>
        <Card.Body>
            <StyledDiv><Image src={Me} fluid roundedCircle /><br /></StyledDiv>
            <Emoji symbol="✨" /> hi i'm bella!<br />
            computer science @ <StyledLink href="https://ucsd.edu/">UC San Diego</StyledLink><br /><br />
            find me ...<br />
            <Emoji symbol="🌶" /> eating spicy food<br />
            <Emoji symbol="💻" /> typing away<br />
            <Emoji symbol="🐶" /> petting my dog<br />

            <br />
            currently → <StyledLink href="https://ucsd.edu">in my last quarter of school :) (WI2020)</StyledLink><br />
            contact → <StyledLink href="mailto:hello@bellabhl.me">hello@bellabhl.me</StyledLink>
        </Card.Body>
        <Card.Footer>
            <StyledLink href={Resume}>resume</StyledLink> • <span></span>
            <StyledLink><Emoji symbol="✨" /> (TBD)</StyledLink>
        </Card.Footer>
    </StyledCard>
);

export default Landing;
